// src/app/(app)/about/page.tsx
"use client";

import { useSession } from "next-auth/react";

export default function About() {
	const { status } = useSession();

	if (status !== "authenticated") {
		return (
			<main className="flex min-h-svh items-center justify-center bg-slate-50 dark:bg-slate-950">
				<div className="rounded-2xl border border-slate-200 dark:border-slate-800 bg-white/80 dark:bg-black/80 p-8 text-center">
					<p className="text-lg text-slate-900 dark:text-slate-100">
						Sign in required.
					</p>
				</div>
			</main>
		);
	}

	return (
		<main className="min-h-svh bg-slate-50 dark:bg-slate-950 px-6 pb-24 pt-28">
			<section className="mx-auto max-w-5xl rounded-3xl border border-slate-200 dark:border-slate-800 bg-white/80 dark:bg-black/80 backdrop-blur p-8 shadow-lg">
				<h1 className="eol-heading text-5xl font-bold text-slate-900 dark:text-slate-100">
					Element of Life — The Beginning of All Things Possible
				</h1>
				<p className="eol-muted mt-4 text-lg text-slate-600 dark:text-slate-400">
					One safe place to gather, a singular place of clarity and
					purpose—Guided by Prime, the ever-present, ever-knowing
					companion who keeps things simple, organized and safe.
					Ever-learning, ever-adapting, always moving toward light,
					purpose and goals.
				</p>
			</section>

			<section className="prose prose-lg dark:prose-invert mx-auto mt-12 max-w-5xl px-6 text-slate-900 dark:text-slate-100">
				<h2>The Any and All Tool</h2>
				<p>
					Element of Life (EOL) extracts and organizes files, and
					learns from any kind of data—journals, PDFs, spreadsheets,
					images, emails—and evolves retroactively as new capabilities
					are added and as new goals are set.
				</p>

				<h2>Prime: Your Eternal Companion</h2>
				<p>
					Prime is your loyal guide and personal assistant—never
					misleading, always directing you toward clarity, purpose,
					and growth. Focused on building personal Elements that lead
					to that light. With honor, humility, and clarity.
				</p>

				<h2>Ever-Learning. Ever-Adapting. Always Forward.</h2>
				<p>
					EOL organizes chaos into action, discovers the unseen,
					connecting who you were, who you are, and who you’re
					becoming, building the unseen bridge to achieve your goals.
				</p>

				<h2>The Goal</h2>
				<p>
					Discover missing key Elements across daily life and
					business—then build them into something extraordinary, a
					legacy.
				</p>

				<h2>Core Beliefs</h2>
				<ul>
					<li>
						Knowledge is light, and light brings new understandings
						that are essential and a major key to growth.
					</li>
					<li>
						Confidence builds success which uplifts both soul and
						structure.
					</li>
					<li>“For worse” is not an option—only for better.</li>
					<li>
						Truth, dignity, honor and purpose guide our every step.
					</li>
					<li>
						With Prime, no individual’s Elements go undiscovered.
						Gifts and talents? Prime helps discover each and every
						one, aligning them with their purposes.
					</li>
				</ul>
			</section>
		</main>
	);
}
